<template lang="pug">
  div.wrapper
    div
      p Кто-то перемешал буквы в наших словах!!!
      p Необходимо угадать правильное слово и собрать его в полях на верху, выбирая правильные буквы
  
    p.getData(@click="getData") Загрузить новое слово!
    div.randomLetterWrapper
      div.randomLetter(v-if="hiddenWord.length > 1" @click="conLog" v-for="letter in hiddenWord" v-model="hiddenWord") {{ letter }}
      div.randomLetter.delBtn(@click="createHiddenWord" title="Сбросить") &#10008;
    img.descriptionImg(:src="responseData.img_src" title="Извините, но этой картинки нет.")
    div.randomLetterWrapper
      div.randomLetter(@click="updateHiddenWord" v-for="letter in shuffledWord") {{ letter.toUpperCase() }}
</template>

<script>
  import * as axios from "axios";
  
  export default {
    name: 'Start',
    props: {},
    data() {
      return {
        responseData: null,
        shuffledWord: null,
        hiddenWord: [],
        counter: 0,
      }
    },
    
    mounted: function () {
      this.getData();
    },
    methods: {
      createHiddenWord() {
        let tempArr = [];
        for (let i = 0; i < this.shuffledWord.length; i++) {
          tempArr[i] = '?';
        }
        this.hiddenWord = tempArr;
      },
      updateHiddenWord(e) {
        if (this.counter < this.shuffledWord.length) {
          this.$set(this.hiddenWord, this.counter, e.target.textContent);
          this.counter++;
          console.log(this.hiddenWord.length);
        } else {
          console.log(this.counter);
        }
        //
      },
      conLog(e) {
      
        
      },
      getData() {
        let randomNumber = Math.floor(Math.random() * (1368 - 1) + 2);
        let url = "https://apidir.pfdo.ru/v1/directory-program-activities/" + randomNumber;
        console.log(url);
        axios
          .get(url)
          .then(response => {
            if (response.data.result_message === 'Запись не найдена') {
              this.getData();
            } else {
              console.log(response.data);
              this.responseData = response.data.data;
            }
          })
          .then(() => {
            this.shuffle(this.responseData.name);
          })
          .then(() => {
            this.createHiddenWord();
          })
        // .catch(function (error) {
        //   console.log(error);
        // })
      },
      //Ф-я перемешивания символов в строке
      shuffle(str) {
        let arr = str.split('');
        let j, temp;
        for (let i = arr.length - 1; i > 0; i--) {
          j = Math.floor(Math.random() * (i + 1));
          temp = arr[j];
          arr[j] = arr[i];
          arr[i] = temp;
        }
        return this.shuffledWord = arr.join('');
      },
    }
  }
</script>

